
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: none;
}
a {
  position: relative;
  cursor: pointer;
}
button {
  cursor: pointer;
}
#app {
  position: relative;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}



